<template>

  <div class="container list-group-item mt-5 mb-5" style="max-width: 500px">

    <p><strong></strong></p>
    <hr class="border-secondary">


    <form>
      <label><strong></strong></label>
      <input v-model="accountId" type="text" class="form-control" placeholder="">
      <div class="text-right">

        <span v-if="idNotExist" class="text-success">   .</span>
        <span v-if="idExist" class="text-danger">  .</span>
        <button @click.prevent="onDoubleCheck" class="btn btn-sm btn-outline-secondary mt-2"></button>

      </div>

      <label><strong></strong></label>
      <input v-model="password" type="password" class="form-control" placeholder="">

      <label><strong></strong></label>
      <input v-model="name" type="text" class="form-control" placeholder="">

      <label><strong></strong></label>
      <input v-model="address" type="text" class="form-control" placeholder="">

      <label><strong></strong></label>
      <input v-model="phone" type="tel" class="form-control" placeholder="">

      <label><strong></strong></label>
      <input v-model="email" type="email" class="form-control" placeholder="">
    </form>

    <div v-if="!isSubmit" class="text-right mt-2">
      <p class="text-danger">   .</p>
    </div>


    <div class="text-right mt-2">
      <button :disabled="!isSubmit" :class="[{'btn-primary':isSubmit},'btn','btn-sm']" @click="isShowModal=true">
      </button>
      <router-link class="btn btn-sm btn-primary" to="/"></router-link>
    </div>

    <Modal v-if="isRegister">
      <h3 slot="header"></h3>
      <div slot="body">
         ?
      </div>
      <span slot="footer" @click="isShowModal=false">
          <button @click="onSubmit" class="btn btn-sm btn-warning"></button>
          <button class="btn btn-sm btn-warning"></button>
      </span>
    </Modal>
  </div>

</template>

<script>

  import {mapActions} from 'vuex'
  import Modal from './Modal'

  export default {

    data() {
      return {

        accountId: '',
        password: '',
        name: '',
        address: '',
        email: '',
        phone: '',

        idExist: false,
        idNotExist: false,
        isSubmit: false,
        isShowModal: false
      }
    },
    components: {
      Modal
    },
    computed: {
      isRegister() {
        return this.isShowModal
      }
    },
    watch: {
      accountId: function (newAccountId) {
        this.isSubmit = false
        this.idExist = false
        this.idNotExist = false
      }
    },
    methods: {
      ...mapActions([
        'JOIN_ID_CHECK',
        'ADD_ACCOUNT'
      ]),
      onDoubleCheck() {
        this.JOIN_ID_CHECK({accountId: this.accountId})
          .then(({status}) => {
            if (status == 409) {
              this.idExist = true
            } else if (status == 200) {
              this.isSubmit = true
              this.idNotExist = true
            }
          })
      },
      onSubmit() {

        console.log(this.accountId)
        this.ADD_ACCOUNT({
          accountId: this.accountId,
          password: this.password,
          name: this.name,
          address: this.address,
          email:this.email,
          phone:this.phone
        }).then((status) => {
          if (status == 400)
            alert("  .")
          else if (status == 200) {
            alert(" .")
            this.$router.push("/")
          }
        })
      }
    }
  }
</script>

<style scoped>

</style>



