<template>
  <div class="container">
    <h5>  </h5>

    <table class="table">
      <thead class="thead-light">
      <tr>
        <th> </th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      </thead>
      <tbody>

      <tr v-for="(item,index) in buyList" :key="index">
        <th scope="row">
          <div class="row">
            <div class="col-sm-auto">
              <img class="img-fluid" style="max-width: 75px" :src="`http://localhost:8090/api/items/download?fileName=${item.itemFileList[0].uuid}_${item.itemFileList[0].fileName}`"  alt="img">
            </div>
            <div class="col-sm-auto">
              <p>{{item.title}}</p>
            </div>
          </div>
        </th>
        <td v-if="item.options && item.options[0].option!==''">{{item.options[0].option}}</td>
        <td v-else></td>
        <td>1</td>
        <td>{{item.price}}</td>
        <td>{{item.discount}}</td>
        <td>{{item.savings}}</td>
        <td>
          <button @click="removeBuy(item.id)" class="btn btn-sm btn-dark"></button>
        </td>
      </tr>

      </tbody>
    </table>
    <p class="text-right">:<strong>{{price}}</strong></p>
    <p class="text-right">:<strong>{{discount}}</strong></p>
    <p class="text-right mt-2">:<strong>{{total}}</strong></p>
    <hr>
  </div>

</template>

<script>
  import {mapState, mapMutations} from 'vuex'

  export default {

    computed: {
      ...mapState([
        'buyList'
      ]),
      total () {
        let sum = 0
        for (let i = 0; i < this.buyList.length; i++) {
          sum += this.buyList[i].price * 1 - this.buyList[i].discount * 1
        }
        return sum
      },
      discount () {
        let sum = 0
        for (let i = 0; i < this.buyList.length; i++) {
          sum += this.buyList[i].discount * 1
        }
        return sum
      },
      price () {
        let sum = 0
        for (let i = 0; i < this.buyList.length; i++) {
          sum += this.buyList[i].price * 1
        }
        return sum
      }
    },
    methods: {
      ...mapMutations([
        'REMOVE_BUY_LIST'
      ]),
      removeBuy (data) {
        this.REMOVE_BUY_LIST(data)
      }
    }
  }
</script>

<style scoped>

</style>



